.profile__name {
  max-width: 336px;
  margin: 0;
  padding: 0;
  font-size: 42px;
  font-weight: 500;
  line-height: 1.14;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 649px) {
  .profile__name {
    text-align: center;
    max-width: 189px;
    font-size: 27px;
    line-height: 1.22;
  }
}
